import React from "react";

export default class Socials extends React.Component{
    constructor(props){
        super(props);
        this.state={};
    }

    render(){
        return(
            <a href={this.props.link}>
            <div className="grid grid-flow-col flex flex-wrap content-center justify-center">
                <div className="text-gray-700 flex flex-wrap content-center font-bold text-left text-2xl">{this.props.title}</div>
                
                    <img src={this.props.image} className="ml-2 md:ml-5 w-24 h-24 text-gray-700 font-bold text-left text-2xl"></img>
                
            </div>
            </a>
        )
    }

}