import React from "react";


export default class TextBox extends React.Component{
    constructor(props){
        super(props);
        this.state={};
    }

    render(){
        return(
            <div className="grid grid-flow-row ml-2 mr-2 md:w-96 border-4 border-gray-700 p-5 rounded-md">
                <div className="text-gray-700 text-5xl text-left font-bold" >{this.props.title}</div>
                <div className="pt-1 pb-1 mt-4 text-gray-500 text-2xl text-left font-bold" >Experience: {this.props.experience}</div>
                <div className="mt-5 text-gray-700 text-left text-3xl"> {this.props.info}</div>
            </div>
        )

    }
} 
