import React from "react";
import FadeIn from 'react-fade-in';

export default class ChaningImage extends React.Component{



    constructor(props)
    {
        super(props)
        this.state={
        };
    }

  

   
    render(){
        return (
            <FadeIn transitionDuration={1000} visible={this.state.visible}>
            <div>
                <img style={{width:"250px"}} className="shadow-xl  rounded-md mb-5" src="https://i.ibb.co/HKLSGrf/hamza.jpg"></img>
            </div>
            </FadeIn>
        )
    }    
}