import React from "react";
import Typewriter from 'react-simple-typewriter'
import FadeIn from "react-fade-in";

export default class InfoParagraph extends React.Component{
    constructor(props){
        super(props);
        this.state={clicked:false};
    }


    handleClick=()=>{
        this.setState({clicked:true});
    }

    render(){
        return (
            <div>
                <div className="grid grid-flow-col flex flex-wrap">
                    <div className="text-yellow-300 text-5xl font-bold text-left" >
                        Work with me . 
                        <Typewriter
                            loop
                            cursor
                            cursorStyle='|'
                            typeSpeed={70}
                            deleteSpeed={200}
                            delaySpeed={1000}
                            words={['Today', 'Tomorrow', 'Now']}
                        />
                    </div>
                </div>
                
                <div className="text-gray-500 pt-3 text-2xl font-normal font-bold text-left">
                    I am a passionate developer ready to make your app ideas come to reality.<br></br> Whenever you need me, im just a message away.
                </div>
                {
                    this.state.clicked?
                    <FadeIn className="pt-5">
                    <div className="m-2 p-5 rounded-md bg-gray-700 shadow-xl text-white font-bold text-xl">
                        Email: hassanhamza930@gmail.com<br></br>Phone:+923174631189
                    </div>
                    </FadeIn>
                    :<button onClick={this.handleClick} className="transition ease-in-out hover:scale-110 hover:bg-gray-700 bg:text-white duration-500 transform pl-10 pr-10 pt-5 pb-5 mt-5 rounded-md bg-blue-400 shadow-xl text-white font-bold text-2xl">
                    Contact
                </button>
                }
            </div>
        )
    }


}